@import url(https://fonts.googleapis.com/css?family=Rubik:300);
/* BODY */
html,
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Acme', sans-serif;
  font-size: 2vh;
  background-color: #323639;
  color: white; }

body {
  position: relative; }

.windowBox {
  max-height: 600px;
  max-width: 100vh; }

/* NAV BAR */
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px; }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47; }

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7; }

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%; }

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em; }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47; }

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em; }

/* Individual item */
.bm-item {
  display: inline-block; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3); }

#shuffle {
  position: relative;
  width: 90%;
  max-width: 80px;
  margin: auto; }
  #shuffle header {
    position: relative;
    padding: 16px;
    height: 25px;
    margin: 4px 0; }
  #shuffle .list-item {
    position: relative;
    margin: 0 6px 12px 6px;
    height: 2vh;
    padding: 16px;
    list-style-type: none; }
    #shuffle .list-item:hover button {
      display: block; }
    #shuffle .list-item.list {
      display: block; }
      #shuffle .list-item.list button {
        top: 0;
        right: 10px;
        bottom: 0;
        margin: auto;
        width: 52px;
        height: 52px;
        border: 0;
        border-radius: 4px;
        color: white;
        font-size: 80px; }
    #shuffle .list-item.grid {
      display: inline-block; }
      #shuffle .list-item.grid button {
        top: 0;
        right: 0;
        font-size: 14px;
        background: transparent;
        border: 0; }
    #shuffle .list-item button {
      display: none;
      position: absolute; }

#standingsHeader {
  margin: 20px 0 0 200px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 200;
  position: fixed;
  margin: 0 0 370px 370px; }

.lgBox {
  position: relative;
  float: left; }

.dvBox {
  width: 30vw;
  position: relative;
  float: left; }

.dvContainer {
  position: absolute;
  min-width: 100vw;
  z-index: 0;
  margin-top: 40px; }

svg {
  font: 12px sans-serif; }

text {
  pointer-events: none;
  color: white; }

.inner_node rect {
  pointer-events: all;
  color: white; }

.inner_node rect.highlight {
  stroke: #315B7E;
  stroke-width: 2px;
  color: white; }

.outer_node circle {
  fill: #282F2E;
  stroke: black;
  stroke-width: 1.5px;
  pointer-events: all;
  color: white; }

.outer_node circle.highlight {
  stroke: #315B7E;
  stroke-width: 2px;
  color: white; }

.link {
  fill: none; }

nav.navbar,
.navbar-default {
  background-color: #2892D7 !important; }

.navbar-default {
  background-image: none;
  border: none;
  border-radius: 0; }

.navbar-default .navbar-nav > li > a {
  color: white; }

.navbar-default .navbar-brand {
  color: white; }

.navbar-default .navbar-toggle {
  border-color: white; }

.navbar-default .navbar-toggle .icon-bar {
  background-color: white; }

.navbar-default .navbar-toggle:focus,
.navbar-default .navbar-toggle:hover {
  background-color: transparent; }

.navbar-default .navbar-brand:focus,
.navbar-default .navbar-brand:hover {
  color: white;
  background-color: transparent; }

.navbar-default .navbar-nav > li > a:focus,
.navbar-default .navbar-nav > li > a:hover {
  color: white;
  background-color: transparent; }

html,
body {
  background: -webkit-radial-gradient(#34495e 0, #19232d 100%);
  background: radial-gradient(#34495e 0, #19232d 100%);
  min-height: 100vh;
  font-family: "Rubik", sans-serif;
  padding: 0;
  margin: 0; }

.description {
  color: #ecf0f1;
  font-size: 22px;
  line-height: 1.4;
  padding: 50px 0;
  text-align: center;
  width: 100%; }
  .description a,
  .description a:hover,
  .description a:active,
  .description a:focus,
  .description a:visited {
    color: #c0cdd1;
    text-decoration: none; }
  .description a:hover {
    border-bottom: 1px solid white; }

.menu-container {
  box-shadow: 0 0px 50px rgba(0, 0, 0, 0.7);
  -webkit-transform: translate(0);
          transform: translate(0);
  margin: 50px auto;
  width: 165px; }

.menu-item,
.menu-item--active {
  height: 60px;
  width: 100%; }

.menu-item {
  align-items: center;
  color: #ecf0f1;
  cursor: pointer;
  display: flex;
  font-size: 14px;
  justify-content: center;
  letter-spacing: 4px;
  text-align: center; }
  .menu-item.active {
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.7); }
  .menu-item:hover {
    color: black;
    text-decoration: none; }
  .menu-item:nth-of-type(1) {
    background: orange; }
  .menu-item:nth-of-type(2) {
    background: #e69500; }
  .menu-item:nth-of-type(3) {
    background: #cc8400; }
  .menu-item:nth-of-type(4) {
    background: #b37400; }
  .menu-item:nth-of-type(5) {
    background: #996300; }
  .menu-item:nth-of-type(6) {
    background: #805300; }

.menu-item--active {
  background: orange;
  border-radius: 2px !important;
  box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
  left: 50%;
  padding: 0 7px;
  position: absolute;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-transition: all 300ms;
  transition: all 300ms;
  z-index: -1; }

